/*Loading*/
.loading-area {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9999;
  overflow: hidden;
}

.loading-area .loading-box {
  width: 100%;
  height: 100%;
  background-color: #FFF;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 1;
  z-index: 9999;
}

.loading-area .loading-pic {
  width: 100%;
  position: absolute;
  top: 50%;
  z-index: 99999;
  text-align: center;
  transform: translateY(-50%);
}

.cssload-spinner {
  position: absolute;
  width: 51px;
  height: 51px;
  left: 50%;
  margin-left: -25px;
  margin-top: -25px;
}

.cssload-spinner:after {
  position: absolute;
  content: "";
  border: 4px solid #ea130e;
  width: 48px;
  height: 48px;
  margin-left: -24px;
  margin-top: -1px;
  animation: cssload-spinner1 2.88s linear infinite;
}

.cssload-spinner:before {
  position: absolute;
  content: "";
  border: 2px solid #000;
  width: 66px;
  height: 66px;
  margin-left: -32px;
  margin-top: -8px;
  animation: cssload-spinner2 2.88s linear infinite;
}

/*Loading Animation Start*/
@keyframes cssload-spinner1 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes cssload-spinner2 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

@keyframes cssload-animation1 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes cssload-animation2 {
  0% {
    transform: scale(0.2);
    left: 0%;
  }
  50% {
    transform: scale(1);
    left: 50%;
  }
  100% {
    transform: scale(0.2);
    left: 100%;
  }
}

/*Loading Animation End*/
