.share-buttons {
    position: fixed;
    width: 300px;
    height: 212px;
    top: 50%;
    right: 0px;
    z-index: 999;
    display: flex;
    justify-content: flex-end;
    overflow: hidden;
  }
  .share-buttons .share-button {
    float: left;
    margin-top: 15px;
  }
  .share-buttons .share-button:first-child {
    margin-top: 0;
    width: 50px;
  }
  .share-buttons .share-button:after {
    clear: both;
    display: table;
  }
  
  .share-button {
    display: block;
    position: relative;
    height: 30px;
  }
  .share-button:hover {
    cursor: pointer;
  }
  .share-button:hover .share-button-primary {
    box-shadow: 1px 0 0 0 rgba(0, 0, 0, 0.1);
  }
  .share-button:hover .share-button-secondary-content {
    transform: translate3d(-100%, 0, 0);
  }
  
  .share-button-primary {
    position: absolute;
    background: #fff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    left: 0;
    top: 50%;
    box-shadow: rgba(0, 0, 0, 0.1);
  }
  .whatsapp{
      background:#06d755;
  }
  
  .share-button-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    position: absolute;
    width: 50px;
    line-height: 50px;
    font-size: 35px;
  }
  
  .share-button-secondary {
    margin-top: 25px;
    margin-left: 15px;
    height: 50px;
  }
  
  .share-button-secondary-content {
    width: 150px;  
    white-space: nowrap;
    font-family: sans-serif;
    font-size: 0.75em;
    background: #fff;
    display: block;
    height: 30px;
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
    line-height: 33px;
    color: #242424;
    border-radius:15px 0 0 15px;
    transform: translate3d(100%, 0, 0);
    transition: transform 175ms ease;
    box-shadow: 0 0 30px 1px rgb(0, 0, 0, 0.1);
  }
